"use client"; // Needed if you use client components like Hero with state or react-query

import Layout from "@/components/Layout";
import Hero from "@/components/Hero";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

const queryClient = new QueryClient();

export const metadata = {
  title: "Commercial Law Chamber - Leading Tax & Commercial Disputes Law Firm in India",
  description:
    "Commercial Law Chamber is a boutique law practice specializing in tax advisory and dispute resolution.",
  keywords:
    "tax law firm, commercial disputes, tax advisory, GST litigation, direct tax, indirect tax",
  openGraph: {
    title: "Commercial Law Chamber - Leading Tax & Commercial Disputes Law Firm",
    description: "Boutique law practice with deep specialization in tax advisory.",
    url: "https://www.clclaw.in",
    images: [
      {
        url: "https://www.clclaw.in/clc-logo.png",
        width: 1200,
        height: 630,
      },
    ],
    locale: "en_IN",
    siteName: "Commercial Law Chamber",
  },
  alternates: {
    canonical: "https://www.clclaw.in",
  },
};

export default function Home() {
  const schema = {
    "@context": "https://schema.org",
    "@type": ["LegalService", "LocalBusiness"],
    name: "Commercial Law Chamber",
    description: "Boutique law practice specializing in tax advisory and dispute resolution",
    url: "https://www.clclaw.in",
    logo: "https://www.clclaw.in/clc-logo.png",
    foundingDate: "2008",
    address: {
      "@type": "PostalAddress",
      streetAddress: "A, 1 A2, Jangpura Rd, Block A",
      addressLocality: "New Delhi",
      addressRegion: "Delhi",
      postalCode: "110014",
      addressCountry: "IN",
    },
  };

  return (
    <QueryClientProvider client={queryClient}>
      <script
        type="application/ld+json"
        dangerouslySetInnerHTML={{ __html: JSON.stringify(schema) }}
      />
      <Layout>
        <Hero />
      </Layout>
    </QueryClientProvider>
  );
}
